@import "./src/assets/themes/appdb/variables.scss";

.textarea {
    background: none;
    border: 1px solid currentColor;
    color: currentColor;
    font-size: inherit;
    font-weight: inherit;
    border-radius: 0.1rem;
    padding: 0.3rem 0.5rem;
    width: 100%;
    resize: vertical;
}

.text {
  white-space: pre-line;
}
